.app {
    text-align: center;
  }

body {
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    margin: 0;
    background-image: linear-gradient(to top, #fbc2eb 0%, #a6c1ee 100%);
    min-height: 100vh;
  }
  
.home {
    padding: 20px;
  }

.heading {
    font-family: 'Copperplate', 'Copperplate Gothic Light', fantasy, 'Lucida Console', monospace;
    font-size: 2.5rem;
    color: #65064a;
    letter-spacing: 5px;
  }

  @media screen and (max-width: 580px) {
    .heading {
      font-size: 1.9rem;
      text-align: right;
      letter-spacing: 3px;
    }
    
  }

  .header {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    gap:10px;
  
  }

  .logo {
    width: 70px;
    height: 70px;
    margin-right: 10px;
  }
  
.task-form {
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
  }
  
.task-input {
    width: 300px;
    padding: 10px;
    margin-right: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
.add-task-button {
    padding: 10px 20px;
    border: none;
    background-color: #28a745;
    color: white;
    border-radius: 4px;
    font-weight: bold;
    cursor: pointer;
  }
  
.add-task-button:hover {
    background-color: #218838;
  }
  
.task-sections {
    display: flex;
    justify-content: space-around;
    margin-top: 20px;
  }
  
.task-section {
    border: 1px solid #ccc;
    border-radius: 14px;
    background-color: white;
    width: 30%;
    padding: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
.task-section h2 {
    text-align: center;
    margin-bottom: 10px;
  }

.task-list li {
  display: flex;
  padding: 10px;
  flex-wrap: wrap;
  list-style:disc;
  border-bottom: 1px solid #ccc;
  justify-content: space-between;
  align-items: center;
}

.task-list li button {
  padding: 5px 10px;
  border: none;
  color: white;
  border-radius: 4px;
  font-weight: bold;
  cursor: pointer;
}

.task-list li button:nth-child(2), .task-list li button:nth-child(3) {
  margin-left: 3px;
}

.to-list li button:first-child {
  background-color: #2a5ddf;
}

.to-list li button:nth-child(2) {
  background-color: #dc3545;  
}

.on-list li button:first-child {
  background-color: #18a043;
}

.on-list li button:nth-child(2) {
  background-color: #dc3545;
  margin-left:6px;
}
.done-list li button:first-child {
  background-color: #2a5ddf;
}

.done-list li button:nth-child(2) {
  background-color: #18a043;
  margin-left:6px;
}

.task-list li button:last-child {
  background-color: #282223;
}

.task-section > button {
  padding: 5px 10px;
  border: none;
  color: rgb(5, 5, 5);
  background-color: #f0e651;
  border-radius: 4px;
  font-weight: bold;
  cursor: pointer;
  position: relative;
  left:70%;
  transition: 0.3s ease-in-out;
}

.task-section > button:hover {
  color: #f0e651;
  background-color: rgb(5, 5, 5);
  border-radius: 6px;
}

@media screen and (max-width: 950px) {
    .task-sections {
      flex-direction: column;
    }
  
    .task-section {
      width: 90%;
      margin-bottom: 20px;
    }

    .task-section > button {
      left: 80%;
    }

    .add-task-button {
      padding: 0 10px;
    }
}

@media screen and (max-width: 480px) {
    .task-input {
      width: 100%;
    }
  
    .task-section > button {
      left: 75%;
    }
  
}